








import { Component, Vue, Prop } from "vue-property-decorator";

@Component({
  name: "ui-toolbar-item"
})
export default class UiToolbarItemComponent extends Vue {
  @Prop({ default: "" })
  icon: string;

  @Prop({ default: true })
  auto: boolean;

  @Prop({ default: false })
  right: boolean;

  @Prop({ default: "" })
  color: string;

  click() {
    this.$emit("click");
  }
}
