














import { Vue, Prop, Component, Watch } from "vue-property-decorator";
import ProgressColor from "./progress-color";

@Component({ name: "ui-progress-bar" })
export default class UiProgressBarComponent extends Vue {
  @Prop({ default: "", type: String })                    text: string;
  @Prop({ default: 100, type: Number })                        max: number;
  @Prop({ default: 50, type: Number })                       value: number;
  
  getValue(): Number {
    return (this.value * 100) / this.max;
  }
  getText() {
    return this.text;
  }
  onClick(){
    
    this.$emit("click");
  }
}
