  //     wrapper.appendChild(imageNode)
  //     this.$refs.editor.appendChild(wrapper);
  //     this.$refs.editor.appendChild(document.createElement("br"));
  //   }

  //     this.manualUpdate(this.$refs.editor.innerHTML);
