import { Component, Vue, Prop, Watch } from "vue-property-decorator";

@Component
export default class BaseInputModel extends Vue {  
  @Prop({type: [Boolean, String, Number, Number, Date, Object, Array] }) 
    value: any | any [] | Boolean | String | Number | string | number | Date;
  @Prop({default: true}) twoWayBinding: boolean;
  @Prop({default: false}) disabled: boolean;
  innerValue: any = this.value;
  //  
  @Watch('value')
  onValueChanged() {
    this.innerValue = this.value;
  }
  //  
  @Watch('innerValue')
  onInnerValueChanged() {
    if (this.twoWayBinding) {
      this.$emit('input', this.innerValue);
    }    
    this.$emit('onChange', this.innerValue);
  }
created() {
  console.log('base');
}
}