export default class MenuFactory {
  private static ProjectMenu = [MenuItemFactory.ProjectTasks, MenuItemFactory.ProjectWays, MenuItemFactory.ProjectQuests, MenuItemFactory.ProjectArtifacts, MenuItemFactory.ProjectDots, MenuItemFactory.ProjectStocks];

  static getProjectMenu(): MenuItem[] {
    this.addParams(this.ProjectMenu, {});
    let menuItems: MenuItem[] = this.ProjectMenu;
    return menuItems;
  }
  static addParams(menu: MenuItem[], params: Object) {
    menu.forEach(item => {
      item.routeParameters = params;
      if (item.childs.length > 0) {
        this.addParams(item.childs, params);
      }
    })
  }
}
