
import EnumMapRouteType from '../../../api/dataSource/api/Artifactum/Entities/Enums/EnumMapRouteType';
import EnumTypeTransmission from '../../../api/dataSource/api/Artifactum/Entities/Enums/EnumTypeTransmission';
import UserMiniInfo from '../../../api/dataSource/api/Artifactum/CrmGuide/IServices/Models/Common/UserMiniInfo';
import EnumMapRouteDetailPassedStatus from '../../../api/dataSource/api/Artifactum/CrmGuide/IServices/Models/MapRoutes/EnumMapRouteDetailPassedStatus';

export default class ModelMapRoutesEdit {
    Id: number;
    Title: string;
    Description: string;
    Distance: number;
    Time: number;
    Type: EnumMapRouteType;
    Transmission: EnumTypeTransmission;
    Price: number;
    Rating: number;
    CommentsCount: number; 
    PointsCount: number; 
    PreviewUrl: string;
    Owner: UserMiniInfo;
    CityName: string;
    CountryName: string;
    MapPointId: number; 
    PassedStatus: EnumMapRouteDetailPassedStatus;
    
    constructor(
        route: ModelMapRoutesEdit
    ) {
        this.Id = route.Id,
        this.Title = route.Title,
        this.Description = route.Description,
        this.Distance = route.Distance,
        this.Time = route.Time,
        this.Type = route.Type,
        this.Transmission = route.Transmission,
        this.Price = route.Price,
        this.Rating = route.Rating,
        this.CommentsCount = route.CommentsCount,
        this.PointsCount = route.PointsCount,
        this.PreviewUrl = route.PreviewUrl,
        this.Owner = route.Owner,
        this.CityName = route.CityName,
        this.CountryName = route.CountryName,
        this.MapPointId = route.MapPointId,
        this.PassedStatus = route.PassedStatus
    }
}